'use client'

import { useState } from 'react'
import { Product } from '@/data/products'

interface CartItem extends Product {
  quantity: number
}

interface CartProps {
  items?: CartItem[]
}

// Datos simulados del carrito
const mockCartItems: CartItem[] = [
  {
    id: 1,
    name: "Camisa Urbana Negra",
    price: 29.99,
    collection: "Camisas",
    images: ["/api/placeholder/400/500"],
    new: true,
    favorite: false,
    quantity: 2
  },
  {
    id: 2,
    name: "Hoodie Minimalista",
    price: 49.99,
    collection: "Hoodies",
    images: ["/api/placeholder/400/500"],
    new: false,
    favorite: true,
    quantity: 1
  }
]

export default function Cart({ items = mockCartItems }: CartProps) {
  const [cartItems, setCartItems] = useState<CartItem[]>(items)

  const updateQuantity = (id: number, newQuantity: number) => {
    if (newQuantity <= 0) {
      setCartItems(cartItems.filter(item => item.id !== id))
    } else {
      setCartItems(cartItems.map(item => 
        item.id === id ? { ...item, quantity: newQuantity } : item
      ))
    }
  }

  const subtotal = cartItems.reduce((sum, item) => sum + (item.price * item.quantity), 0)
  const shipping = subtotal > 50 ? 0 : 5.99
  const total = subtotal + shipping

  if (cartItems.length === 0) {
    return (
      <div className="bg-gray-900 rounded-lg p-6">
        <h2 className="text-xl font-semibold text-white mb-4">Tu Carrito</h2>
        <div className="text-center py-8">
          <div className="text-gray-500 text-4xl mb-4">üõí</div>
          <p className="text-gray-400">Tu carrito est√° vac√≠o</p>
        </div>
      </div>
    )
  }

  return (
    <div className="bg-gray-900 rounded-lg p-6">
      <h2 className="text-xl font-semibold text-white mb-6">Tu Carrito</h2>
      
      {/* Cart Items */}
      <div className="space-y-4 mb-6">
        {cartItems.map((item) => (
          <div key={item.id} className="flex items-center space-x-4 bg-gray-800 rounded-lg p-4">
            <div className="w-16 h-16 bg-gray-700 rounded-md flex items-center justify-center">
              <span className="text-gray-400 text-xs">IMG</span>
            </div>
            
            <div className="flex-1">
              <h3 className="text-white font-medium text-sm">{item.name}</h3>
              <p className="text-gray-400 text-xs">{item.collection}</p>
              <p className="text-red-600 font-semibold">${item.price.toFixed(2)}</p>
            </div>
            
            <div className="flex items-center space-x-2">
              <button
                onClick={() => updateQuantity(item.id, item.quantity - 1)}
                className="w-8 h-8 bg-gray-700 hover:bg-gray-600 text-white rounded-md flex items-center justify-center transition-colors"
              >
                -
              </button>
              <span className="text-white w-8 text-center">{item.quantity}</span>
              <button
                onClick={() => updateQuantity(item.id, item.quantity + 1)}
                className="w-8 h-8 bg-gray-700 hover:bg-gray-600 text-white rounded-md flex items-center justify-center transition-colors"
              >
                +
              </button>
            </div>
          </div>
        ))}
      </div>

      {/* Cart Summary */}
      <div className="border-t border-gray-700 pt-4 space-y-2">
        <div className="flex justify-between text-gray-300">
          <span>Subtotal:</span>
          <span>${subtotal.toFixed(2)}</span>
        </div>
        <div className="flex justify-between text-gray-300">
          <span>Env√≠o:</span>
          <span>{shipping === 0 ? 'Gratis' : `$${shipping.toFixed(2)}`}</span>
        </div>
        {shipping === 0 && (
          <p className="text-green-500 text-xs">¬°Env√≠o gratis por compras mayores a $50!</p>
        )}
        <div className="flex justify-between text-white font-semibold text-lg border-t border-gray-700 pt-2">
          <span>Total:</span>
          <span>${total.toFixed(2)}</span>
        </div>
      </div>
    </div>
  )
}
