'use client'

import { useState } from 'react'
import { useForm } from 'react-hook-form'
import Cart from '@/components/ui/Cart'

interface OrderForm {
  name: string
  email: string
  phone: string
  address: string
  comments?: string
}

export default function PedidosPage() {
  const [showConfirmation, setShowConfirmation] = useState(false)
  const [orderData, setOrderData] = useState<OrderForm | null>(null)

  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
    reset
  } = useForm<OrderForm>()

  const onSubmit = async (data: OrderForm) => {
    // Simular procesamiento del pedido
    await new Promise(resolve => setTimeout(resolve, 1000))
    
    setOrderData(data)
    setShowConfirmation(true)
    reset()
  }

  const closeConfirmation = () => {
    setShowConfirmation(false)
    setOrderData(null)
  }

  return (
    <div className="min-h-screen bg-black py-8">
      <div className="max-w-7xl mx-auto px-4">
        {/* Page Header */}
        <div className="text-center mb-12">
          <h1 className="text-4xl md:text-5xl font-bold text-white mb-4">
            Realizar Pedido
          </h1>
          <p className="text-gray-400 text-lg">
            Completa tu informaci√≥n para finalizar la compra
          </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Cart Section */}
          <div className="order-2 lg:order-1">
            <Cart />
          </div>

          {/* Order Form Section */}
          <div className="order-1 lg:order-2">
            <div className="bg-gray-900 rounded-lg p-6">
              <h2 className="text-xl font-semibold text-white mb-6">
                Informaci√≥n de Entrega
              </h2>

              <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
                {/* Name Field */}
                <div>
                  <label htmlFor="name" className="block text-sm font-medium text-gray-300 mb-1">
                    Nombre Completo *
                  </label>
                  <input
                    {...register('name', { 
                      required: 'El nombre es requerido',
                      minLength: { value: 2, message: 'El nombre debe tener al menos 2 caracteres' }
                    })}
                    type="text"
                    id="name"
                    className="w-full px-3 py-2 bg-gray-800 border border-gray-700 rounded-md text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-red-600 focus:border-transparent"
                    placeholder="Tu nombre completo"
                  />
                  {errors.name && (
                    <p className="mt-1 text-sm text-red-500">{errors.name.message}</p>
                  )}
                </div>

                {/* Email Field */}
                <div>
                  <label htmlFor="email" className="block text-sm font-medium text-gray-300 mb-1">
                    Correo Electr√≥nico *
                  </label>
                  <input
                    {...register('email', { 
                      required: 'El correo es requerido',
                      pattern: {
                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                        message: 'Correo electr√≥nico inv√°lido'
                      }
                    })}
                    type="email"
                    id="email"
                    className="w-full px-3 py-2 bg-gray-800 border border-gray-700 rounded-md text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-red-600 focus:border-transparent"
                    placeholder="tu@email.com"
                  />
                  {errors.email && (
                    <p className="mt-1 text-sm text-red-500">{errors.email.message}</p>
                  )}
                </div>

                {/* Phone Field */}
                <div>
                  <label htmlFor="phone" className="block text-sm font-medium text-gray-300 mb-1">
                    Tel√©fono *
                  </label>
                  <input
                    {...register('phone', { 
                      required: 'El tel√©fono es requerido',
                      minLength: { value: 10, message: 'El tel√©fono debe tener al menos 10 d√≠gitos' }
                    })}
                    type="tel"
                    id="phone"
                    className="w-full px-3 py-2 bg-gray-800 border border-gray-700 rounded-md text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-red-600 focus:border-transparent"
                    placeholder="+1 (555) 123-4567"
                  />
                  {errors.phone && (
                    <p className="mt-1 text-sm text-red-500">{errors.phone.message}</p>
                  )}
                </div>

                {/* Address Field */}
                <div>
                  <label htmlFor="address" className="block text-sm font-medium text-gray-300 mb-1">
                    Direcci√≥n de Entrega *
                  </label>
                  <textarea
                    {...register('address', { 
                      required: 'La direcci√≥n es requerida',
                      minLength: { value: 10, message: 'La direcci√≥n debe ser m√°s espec√≠fica' }
                    })}
                    id="address"
                    rows={3}
                    className="w-full px-3 py-2 bg-gray-800 border border-gray-700 rounded-md text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-red-600 focus:border-transparent resize-none"
                    placeholder="Calle, n√∫mero, ciudad, c√≥digo postal..."
                  />
                  {errors.address && (
                    <p className="mt-1 text-sm text-red-500">{errors.address.message}</p>
                  )}
                </div>

                {/* Comments Field */}
                <div>
                  <label htmlFor="comments" className="block text-sm font-medium text-gray-300 mb-1">
                    Comentarios Adicionales
                  </label>
                  <textarea
                    {...register('comments')}
                    id="comments"
                    rows={2}
                    className="w-full px-3 py-2 bg-gray-800 border border-gray-700 rounded-md text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-red-600 focus:border-transparent resize-none"
                    placeholder="Instrucciones especiales, preferencias de entrega..."
                  />
                </div>

                {/* Submit Button */}
                <button
                  type="submit"
                  disabled={isSubmitting}
                  className="w-full bg-red-600 hover:bg-red-700 disabled:bg-red-800 disabled:cursor-not-allowed text-white font-semibold py-3 px-4 rounded-md transition-colors duration-200"
                >
                  {isSubmitting ? 'Procesando...' : 'Realizar Pedido'}
                </button>

                <p className="text-xs text-gray-400 text-center">
                  Al realizar el pedido, aceptas nuestros t√©rminos y condiciones
                </p>
              </form>
            </div>
          </div>
        </div>
      </div>

      {/* Confirmation Modal */}
      {showConfirmation && (
        <div className="fixed inset-0 bg-black bg-opacity-75 flex items-center justify-center z-50 p-4">
          <div className="bg-gray-900 rounded-lg p-8 max-w-md w-full">
            <div className="text-center">
              <div className="text-green-500 text-5xl mb-4">‚úì</div>
              <h3 className="text-2xl font-bold text-white mb-4">
                ¬°Pedido Confirmado!
              </h3>
              <p className="text-gray-300 mb-6">
                Gracias {orderData?.name}, tu pedido ha sido recibido y ser√° procesado pronto.
              </p>
              <div className="bg-gray-800 rounded-lg p-4 mb-6 text-left">
                <h4 className="text-white font-semibold mb-2">Detalles del pedido:</h4>
                <p className="text-gray-300 text-sm">Email: {orderData?.email}</p>
                <p className="text-gray-300 text-sm">Tel√©fono: {orderData?.phone}</p>
                <p className="text-gray-300 text-sm">Direcci√≥n: {orderData?.address}</p>
              </div>
              <button
                onClick={closeConfirmation}
                className="bg-red-600 hover:bg-red-700 text-white px-6 py-2 rounded-md font-semibold transition-colors"
              >
                Cerrar
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
